.App {
  text-align: center;
  width: 375px;
  margin: auto;
  min-height: 100vh;
  margin: 3% auto;
  border-radius: 20px;
  box-shadow: 2px 2px 19px #aaa;
}
i.fa-arrow-circle-left {
  font-size: 25px;
  float: left;
  padding-left: 20px;
}
i.fa-arrow-circle-left:hover {
  cursor: pointer;
}
.erBbkF {
  position: unset !important;
}
@media only screen and (max-width: 1020px) {
  .App {
    width: 100%;
    margin: 0px;
  }
}