.customer {
    padding: 30px 10px 10px 10px;
}
.customer .cust-list:hover {
    cursor: pointer;
}
.customer button.btn-action {
    width: 80%;
    text-transform: unset;
}
.customer .cust-list p:first-child {
    margin: 10px 0px 0px 0px;
}
.customer .cust-list p:last-child {
    margin: 0px 0px 10px 0px;
}